body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to top left, #f9f3e4, #959188);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-img {
  border-radius: 50%;
  box-shadow: 10px 10px 5px #797289;
}

.main {
  display: flex;
  flex-direction: row;
  margin-top: 5%;
  justify-content: space-around;
}

.content {
  display: flex;
  flex-direction: column;
  margin-right: 5%;
}

.heading {
  color: #39546c;
  font-size: 4rem;
  text-shadow: 2px 2px;
}

.list > li {
  color: #797289;
  font-size: 1.5rem;
}

footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background: linear-gradient(to top left, #f9f3e4, #959188);
  color: #5a748c;
  text-align: center;
}
